.calculator-change-sector {
  // .calculator-change-sector__title
  &__title {
    margin-bottom: 25px;

    color: $text-color;
    font-weight: 400;
  }

  // .calculator-change-sector__container

  &__container {
    position: relative;

    padding: 0 92px;
  }

  // .calculator-change-sector__item

  &__item {
    width: 316px;
    overflow: hidden;
    
    border: 1px solid rgba($color: #000000, $alpha: 0);
    
    border-radius: 4px;
    box-shadow: 0px 30px 40px rgba(121, 179, 220, 0.06);
    
    cursor: pointer;

    transition: all .3s ease-in-out;
    &-container{
      display: flex;
      justify-content: center;

      border-color: $primary-color;
    }
    &_active{
      .calculator-change-sector__item{
        border: 1px solid $primary-color;
        border-radius: 4px;
        box-shadow: 0px 30px 60px rgba(74, 183, 174, 0.08);
      }
    }
  }

  // .calculator-change-sector__item-image

  &__item-image {
    display: flex;
    width: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .calculator-change-sector__item-content

  &__item-content {
    padding: 24px 11px 20px;

    background: #FFFFFF;
  }

  // .calculator-change-sector__item-title

  &__item-title {
    margin-bottom: 2px;

    color: $text-color;
    font-weight: 500;
    line-height: 1;
  }
  
  // .calculator-change-sector__item-text
  
  &__item-text {
    color: $text-color;
    font-weight: 300;
  }

  // .calculator-change-sector__button
  &__button {
    position: absolute;
    top: 50%;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 60px;
    height: 60px;
    margin-top: 10px;

    background: none;
    border: 1px solid $primary-color;
    border-radius: 2px;
    transform: translate(0, -50%);
    cursor: pointer;

    transition: all .3s ease-in-out;
    &:hover:not(&_disabled){
      background: $primary-color;
      svg{
        fill:#FFF
      }
    }
    svg{
      transition: all .3s ease-in-out;

      fill: $primary-color;
    }
   
    // .calculator-change-sector__button_prev
    &_prev {
      left: 0;
    }

    // .calculator-change-sector__button_next
    &_next {
      right: 0;
    }

    // .calculator-change-sector__button_disabled
    &_disabled {
      border-color:#B6BDC8;

      pointer-events: none;
      svg{
        fill: #B6BDC8;
      }
    }
  }
}

// <1280
@include media-laptop {

}
// <992
@include media-tablet-horizontal {}
// <768
@include media-tablet {
  .calculator-change-sector{
    &__container{
      padding: 0;
    }
    &__navigation{
      display: flex;
      margin-top: 20px;
    }
    &__button{
      position: relative;

      margin-top: 0;

      transform: translate(0);
      &:first-child{
        margin-right: 10px;
      }
    }
  }
}
