.call-me {
  position: relative;
  &__label{
    position: relative;

    width: 100%;
  }
  &__input {
    box-sizing: border-box;
    width: 100%;
    padding: 29.5px 40px 29.5px 68px;

    color: $text-dark-color;
    font-weight: normal;
    font-family: Stem;
    font-style: normal;
    line-height: 21px;
    letter-spacing: 0.02em;

    background: #FFFFFF;
    border: 1px solid #EFEFEF;
  }
  &__input::placeholder{
    color: $text-dark-color;
    font-weight: normal;
    font-family: Stem;
    font-style: normal;
    line-height: 21px;
    letter-spacing: 0.02em;
  }

  &__submit {
    padding: 28.5px 81.5px;

    color: #FFFFFF;
    font-weight: 500;
    font-family: Stem;
    font-style: normal;
    line-height: 23px;
    letter-spacing: 0.02em;

    background: $primary-color;
    border: none;
    border-radius: 2px;
    box-shadow: 0px 80px 118px rgba(0, 0, 0, 0.05), 0px 29.2013px 43.0719px rgba(0, 0, 0, 0.06), 0px 14.1767px 20.9106px rgba(0, 0, 0, 0.06), 0px 6.94968px 10.2508px rgba(0, 0, 0, 0.04), 0px 2.74791px 4.05317px rgba(0, 0, 0, 0.0434026);
    cursor: pointer;

    appearance: none;
  }
  &__img {
    position: absolute;
    top: 50%;
    left: 40px;

    transform: translate(0, -50%);
  }
}
.call-me-content {
  padding: 140px 0px 89px 0px;
}
.call-me-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 33px;
  &_text {
    color: $text-color;
    font-weight: 500;
    font-style: normal;
    line-height: 52px;
  }

  &__tel {
    &_phone {

      position: relative;

      color: $text-color;
      font-weight: normal;
      font-style: normal;
      line-height: 49px;
      text-decoration: none;

      outline: none;
      cursor: pointer;
      &::after{
        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;

        background: $primary-color;

        content: '';

      }
    }

    &_line {
      width: 314px;
      height: 1px;

      background: $primary-color;
    }
  }
}
.call-me-block {
  display: flex;
  align-items: center;
}
.call-me-fon {
  position: absolute;
  top: 0;
  z-index: -2;

  width: 100%;
  height: 100%;

  background-image: url(../images/form-bg.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.call-me-bg {
  position: absolute;
  top: 0;
  z-index: -1;

  width: 100%;
  height: 100%;

  background: rgba(255, 255, 255, 0.88);
  backdrop-filter: blur(2px);
}


// <1280
@include media-laptop {
  .call-me-content{
    padding: 80px 0;
  }
}
// <992
@include media-tablet-horizontal {}
// <768
@include media-tablet {
  .call-me{
    &__submit{
      padding: 18.5px 14.5px;
    }
    &__input{
      padding: 18.5px 10px 18.5px 40px;
    }
    &__img{
      left: 10px;
    }
  }
}
// <480
@include media-mobile {
  .call-me-block{
    flex-direction: column;
    align-items: stretch;
  }
  .call-me-title{
    flex-direction: column;
  }
}
// <320
@include media-mobile-small {}