.calendar {
  padding-top: 70px;
  padding-bottom: 70px;
}
// fix Kanto
.fix-width{
  display: flex;
  justify-content: center;
}
.calendar-arrow-left {
  margin-right: 49px;
  cursor: pointer;
}
.calendar-arrow-right {
  margin-left: 49px;
  cursor: pointer;
}
// fix Kanto
.calendar-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__data {
    display: flex;
    flex-direction: column;
    // align-items: center; 
    margin-right: 80px;
    flex: 1 1 auto;
    padding: 32px 32px 38px 32px;

    background: linear-gradient(231.97deg, #84CDC7 0%, #074064 100%);
    border-radius: 40px;
    box-shadow: 0px 20px 40px rgba(27, 136, 127, 0.1);

    thead, tbody, tr{
      width: 100%;
    }
  }

  &__data-title {
    display: flex;
    align-items: center; 
    margin-bottom: 32px;
    &_left {
      margin-right: 49px;

      cursor: pointer;
    }

    &_right {
      margin-left: 49px;

      transform: rotate(180deg);
      cursor: pointer;
    }
  }

  &__data-title-mout {

    &_text {
      
      color: #FFFFFF;
      font-weight: bold;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__data-week {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 28px;
    text-align: center;
    &_text {
      flex: 0 0 calc(100% / 7);
      color: #FFFFFF;
      font-weight: bold;
      font-style: normal;
      line-height: 160%;
      cursor: pointer;
      &.today{
        position:relative;
        color: $text-color;
        z-index: 1;
        &::after{
          content: '';
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 32px;
          height: 32px;
          border-radius: 8px;
          background: #FFFFFF;
          z-index: -1;
        }
      }
    }
    &_text:active{
      position:relative;
      color: $text-color;
      z-index: 1;
      &::after{
          content: '';
          position:  absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 32px;
          height: 32px;
          border-radius: 8px;
          background: #ffffffa4;
          z-index: -1;
        }
    }

  }

  &__data-day {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    max-width: 584px;

    transform: translateX(30px);
    &_link {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      margin-right: 49px;
      margin-bottom: 28px;

      color: #FFFFFF;
      font-weight: 350;
      font-style: normal;
      line-height: 160%;

      cursor: pointer;
    }
    &_link:active{
      color: $text-color;

      background: #FFFFFF;
      border-radius: 8px;
    }
    &_link:first-child{
      cursor: none;
    }
  }



  &__info-title {
    max-width: 445px;
    margin-bottom: 13px;

    color: $text-color;
    font-weight: 500;
    font-style: normal;
    line-height: 140%;
  }

  &__info-text {
    max-width: 390px;

    color: $text-color;
    font-weight: 350;
    font-style: normal;
    line-height: 140%;
  }

  &__info-content {
    display: flex;
    flex-direction: column;
    margin-top: 26px;
  }

  &__info-time {
    position: relative;

    margin-bottom: 12px;
    &_timer {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    &_input {
      box-sizing: border-box;
      width: 100%;
      padding: 21.5px 40px 21.5px 68px;

      color: $text-dark-color;
      font-weight: normal;
      font-size: 16px;
      font-style: normal;
      line-height: 21px;
      letter-spacing: 0.02em;
      background: none;
      border: 1px solid #4AB7AE;
      cursor: pointer;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    }

    &_arrow {
      position: absolute;
      top: 26px;
      right: 20px;
    }
  }

  &__info-phone {
    position: relative;

    margin-bottom: 12px;
    &_img {
      position: absolute;
      top: 20px;
      left: 20px;
    }
    &_input {
      box-sizing: border-box;
      width: 100%;
      padding: 21.5px 40px 21.5px 68px;

      color: $text-dark-color;
      font-weight: normal;
      font-size: 16px;
      font-style: normal;
      line-height: 21px;
      letter-spacing: 0.02em;

      border: 1px solid #4AB7AE;
    }

  }

  &__info-name {
    position: relative;

    margin-bottom: 12px;

    &_img {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    &_input {
      box-sizing: border-box;
      width: 100%;
      padding: 21.5px 40px 21.5px 68px;

      color: $text-dark-color;
      font-weight: normal;
      font-size: 16px;
      font-style: normal;
      line-height: 21px;
      letter-spacing: 0.02em;

      border: 1px solid #4AB7AE;
    }
  }

  &__info-btn {
    width: 100%;
    padding: 20.5px 0px 20.5px 0px;

    color: #FFFFFF;
    font-weight: 500;
    font-size: 18px;
    font-style: normal;
    line-height: 23px;
    letter-spacing: 0.02em;
    text-align: center;

    background: #4AB7AE;
    border: none;
    box-shadow: 0px 80px 118px rgba(0, 0, 0, 0.05), 0px 29.2013px 43.0719px rgba(0, 0, 0, 0.06), 0px 14.1767px 20.9106px rgba(0, 0, 0, 0.06), 0px 6.94968px 10.2508px rgba(0, 0, 0, 0.04), 0px 2.74791px 4.05317px rgba(0, 0, 0, 0.0434026);
    cursor: pointer;
  }
}

//1200px
@include media-laptop {
    .calendar-block{
      flex-direction: column-reverse;
    }
    .calendar{
      padding-top: 35px;
      padding-bottom: 35px;
    }
    .calendar-block__info{
      margin-bottom: 40px;
    }
    .calendar-block__data{
      margin-right: 0;
      width: 100%;
    max-width: 800px;
    }
}
//992px
@include media-tablet-horizontal {
  .calendar-block__data{
    width: 100%;
  }
}
//768px
@include media-tablet {
  .calendar-block__data-day{
    width: 100%;
    transform: translateX(0px);

  }
  .calendar-block__data{
    padding: 32px 10px 38px 10px;
  }
  .calendar-block__data-day_link{
  }
  .calendar-block__data-week {
    justify-content: flex-start;

  }
  .calendar-block__data-week_text{
  }
}
