.solutions-for-buying {
  padding: 80px 0 ;
  // .solutions-for-buying__title

  &__title {
    margin-bottom: 60px;
  }

  // .solutions-for-buying__row

  &__row {
    flex-wrap: wrap;
    width: 100%;
  }

  // .solutions-for-buying__item

  &__item {
    display: inline-block;
    max-width: 279px;

    text-decoration: none;

    background: #FFFFFF;
    border-radius: 1px;
    box-shadow: 0px 12px 40px rgba(166, 208, 238, 0.16);
    &-container{
      margin-right: 5px;
    }
  }

  // .solutions-for-buying__item-image

  &__item-image {
    display: flex;
  }

  // .solutions-for-buying__item-text

  &__item-text {  
    padding: 16px 20px 22px;

    color: $primary-dark-color;
    font-weight: normal;
    line-height: 140%;
  }
}


// <1280
@include media-laptop {
  .solutions-for-buying{
    padding: 50px 0;
    &__title{
      margin-bottom: 30px;
    }
    &__row{
      justify-content: center;
      margin: 0 auto;
    }
    &__item{
      &-container{
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 5px;
      }
    }
  }
}
// <768
@include media-tablet {
  .solutions-for-buying{
    padding: 30px 0;
  }
}