.learn-more-technology {
  padding: 80px 0;
  // .learn-more-technology__title
  &__title {
    margin-bottom: 24px;
  }

  // .learn-more-technology__tabs

  &__tabs {
    display: flex;
    margin-bottom: 59px;
  }

  // .learn-more-technology__tab

  &__tab {
    width: 234px;
    padding: 24px 5px;

    color: $text-dark-color;
    font-weight: 300;

    background: rgba(255, 255, 255, 0.3);

    border: none;
    border-radius: 2px;

    transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, background 0.3s ease-in-out;
    // .learn-more-technology__tab_active
    &_active {
      color: $primary-color;
      font-weight: 400;

      background: #FFFFFF;
      box-shadow: 4px 4px 50px rgba(166, 208, 238, 0.2);

      pointer-events: none;
    }
  }

  // .learn-more-technology__content

  &__content {
    position: relative;

    display: none;
    padding: 0 50px 39px;

    opacity: 0;
    &_active {
      display: flex;

      opacity: 1;
    }
    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;

      width: 100%;
      height: 459px;

      border: 1px solid $primary-color;
      border-radius: 2px;

      content: '';
    }
  }

  // .learn-more-technology__item

  &__item {
    flex: 1 1 468px;
    &:first-child {
      flex: 1 1 585px;
      margin-right: 20px;
    }
  }
}

.learn-more-technology-item {
  padding: 28px 19px 46px;

  background: #FFFFFF;
  border-radius: 2px;
  box-shadow: 0px 12px 40px rgba(121, 179, 220, 0.1);

  // .learn-more-technology-item_inactive
  &_inactive {
    margin-top: 45px;
    padding: 16px 16px 90px;

    box-shadow: 0px 12px 40px rgba(121, 179, 220, 0.1);
    .learn-more-technology-item__image {
      margin-bottom: 16px;
    }
    .learn-more-technology-item__title {
      margin-bottom: 12px;
      padding-left: 9px;

      opacity: 0.6;
    }
  }

  // .learn-more-technology-item__image

  &__image {
    margin-bottom: 24px;
    img {
      width: 100%;
      height: auto;
    }
  }

  // .learn-more-technology-item__title

  &__title {
    margin-bottom: 21px;
    padding: 0 12px;

    color: $text-color;
    font-weight: 400;
  }

  // .learn-more-technology-item__list

  &__list {
    padding: 0 12px 0 30px;
  }

  // .learn-more-technology-item__advantage

  &__advantage {
    margin-bottom: 12px;
    padding-left: 5px;

    list-style-image: url(../images/svg/icons/ic.svg);

    &:last-child {
      margin-bottom: 0;
    }
  }

  // .learn-more-technology-item__advantage-title

  &__advantage-title {
    margin-bottom: 5px;

    font-weight: 350;
  }

  // .learn-more-technology-item__advantage-description

  &__advantage-description {
    font-weight: 300;
  }

  // .learn-more-technology-item__description

  &__description {
    padding-left: 7px;

    font-weight: 300;

    line-height: 165%;

    opacity: 0.6;
  }
}

// <1280
@include media-laptop {
  .learn-more-technology{
    padding: 60px 0;
    &__content{
      padding: 0px 25px 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .learn-more-technology{
    padding: 50px 0;
    &__content{
      padding: 0px 19px 30px;
    }
  }
}
// <768
@include media-tablet {
  .learn-more-technology{
    padding: 30px 0;
    &__tabs{
      flex-wrap: wrap;
      margin-bottom: 30px;
    }
    &__tab{
      flex: 1 1 33.33%;
    }
    &__content{
      flex-direction: column;
      align-items: center;
      &::after{
        height: 90%;
      }
    }
    &__item{
      &:first-child{
        margin-right: 0;
      }
    }
  }
}
// <480
@include media-mobile {.learn-more-technology{
    &__tabs{
      margin-bottom: 20px;
    }
    &__tab{
      flex: 1 1 50%;
    }
    &__content{
      padding: 0px 10px 20px;
    }
    &__item{
      &:first-child{
        margin-right: 0;
      }
    }
  }
  .learn-more-technology-item{
    padding: 10px;
    &_inactive{
      margin-top: 25px;
    }
    &__image{
      margin-bottom: 20px;
    }
  }
}
// <320
@include media-mobile-small {
}
