.partners {
  padding: 40px 0 39px;
  // .partners__title
  &__title {
    margin-bottom: 34px;
    margin-left: 8px;
  }

  // .partners__list
  &__list {
    width: 100%;
    margin-bottom: 24px;
  }

  // .partners__item
  &__item {
    flex: 1 1 100%;
    padding: 40px 10px;

    background: #FFF;
    border-right: 1px solid #F5F5F5;
  }

  // .partners__text

  &__text {
    color: $text-color;
    font-weight: 350;
  }
}

// <1280
@include media-laptop {
  .partners {
    padding: 30px 0;
    &__title {
      margin-bottom: 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .partners{
    &__item{
      padding: 20px 10px;
      img,
      picture {
        width: 80%;
        height: auto;
        margin: 0 auto;
      }
    }
  }
}
// <768
@include media-tablet {
  .partners{
    &__list{
      flex-wrap: wrap;
    }
    &__item{
      flex-basis: 50%;
      margin-top: 2px;
    }
  }
}
// <480
@include media-mobile { .partners{
    &__item{
      flex-basis: auto;
      &:first-child{
        margin-top: 0;
      }
    }
  }
}
