.advantage {
  padding-top: 67px;
  padding-bottom: 20px;

  background: #F5F7F8;
}
.advantage-wrapper {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  .swiper-container {
    max-width: 100%;
  }
}
.advantage-title {
  margin-bottom: 80px;

  color: #3E516D;
  font-weight: 500;
  font-style: normal;
  line-height: 160%;
}

.advantage-description-number {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  &_val {
    color: #4AB7AE;
    font-weight: 500;
    font-size: 44px;
    font-style: normal;
    line-height: 160%;
  }

  &_line {
    width: 1px;
    height: 56px;
    margin-right: 24px;
    margin-left: 24px;

    background: #4AB7AE;
  }

  &_name {
    color: #3E516D;
    font-weight: 500;
    font-size: 24px;
    font-style: normal;
    line-height: 160%;
  }
}
.advantage-description-text {
  max-width: 376px;

  color: #3E516D;
  font-weight: 350;
  font-style: normal;
  line-height: 160%;
}
.advantage-description-bottom {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}
.advantage-description-bottom-line {
  position: relative;

  width: 100%;
  height: 1px;

  background: #EFEFEF;
  &_elem {
    position: absolute;

    width: 80px;
    height: 1px;

    background: #4AB7AE;
  }
}
.advantage-description-bottom-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 10px;
  &_text {
    color: #4AB7AE;
    font-weight: normal;
    font-style: normal;
    line-height: 160%;
  }
}
.advantage-description-bottom-item-buttons {
  display: flex;
  align-items: center;
}
.advantage-description-bottom-item-btn {
  background: none;
  border: none;
  opacity: 1;

  transition: opacity 0.3s ease-in-out;
  &_left {
    margin-right: 20px;

    cursor: pointer;
  }

  &_right {
    cursor: pointer;
  }
  &_disabled {
    opacity: 0.5;

    pointer-events: none;
  }
}
.advantage-fon {
  position: relative;

  max-width: 725px;
  &_home {
    width: 725px;

    transform: translateX(10px);
  }

  &_floor {
    position: absolute;
    bottom: -10px;
    left: 0;

    width: 668px;
  }
}
.advantage-fon-block {
  position: absolute;
  top: 0;

  display: flex;
}
.advantage-fon-block-two {
  position: absolute;
  bottom: 10px;

  display: flex;
}
.advantage-fon-wrapper {
  display: flex;
  flex-direction: column;
}
.advantage-fon-block-elem {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 68px;
  height: 68px;

  color: $text-color;
  text-decoration: none;

  background: rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  cursor: pointer;
  backdrop-filter: blur(4px);

  transition: all 0.3s ease-in-out;
  &:hover {
    .advantage-fon-block-elem-item {
      color: #FFFFFF;

      background: #62E7DCE1;
    }
  }
  &_active {
    pointer-events: none;
    .advantage-fon-block-elem-item {
      color: #FFFFFF;

      background: #4AB7AE;
    }
  }
}
.advantage-fon-block {
  .advantage-fon-block-elem:nth-child(1) {
    transform: translate(170px, 112px);
  }
  .advantage-fon-block-elem:nth-child(2) {
    transform: translate(230px, 47px);
  }
  .advantage-fon-block-elem:nth-child(3) {
    transform: translate(380px, 100px);
  }
}
.advantage-fon-block-two {
  .advantage-fon-block-elem:nth-child(1) {
    transform: translate(78px, -145px);
  }
  .advantage-fon-block-elem:nth-child(2) {
    transform: translate(340px, -115px);
  }
  .advantage-fon-block-elem:nth-child(3) {
    transform: translate(365px, -120px);
  }
}

.advantage-fon-block-elem-item {
  position: absolute;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;

  background: #FFFFFF;
  border-radius: 100%;
  cursor: pointer;

  transition: all 0.3s ease-in-out;

}

//1200px
@include media-laptop {
}
//992px
@include media-tablet-horizontal {
  .advantage-wrapper {
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
}
//768px
@include media-tablet {
  .advantage {
    padding-top: 35px;
  }
  .advantage-fon_home {
    width: 460px;
  }
  .advantage-fon_floor {
    width: 410px;
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(1) {
    transform: translate(38px, -81px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(1) {
    transform: translate(123px, 60px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(2) {
    transform: translate(123px, 5px);
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(2) {
    transform: translate(189px, -69px);
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(3) {
    transform: translate(152px, -194px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(3) {
    transform: translate(216px, 158px);
  }
  .advantage-title {
    line-height: 120%;
  }
}
// 480
@include media-mobile {
  .advantage-fon-block-elem {
    width: 34px;
    height: 34px;
  }
  .advantage-fon-block-elem-item {
    width: 24px;
    height: 24px;
  }
  .advantage-fon_home {
    width: 300px;
  }
  .advantage-fon_floor {
    width: 280px;
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(1) {
    transform: translate(30px, -58px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(1) {
    transform: translate(77px, 46px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(2) {
    transform: translate(102px, 24px);
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(3) {
    transform: translate(141px, -122px);
  }
  .advantage-fon-block-two .advantage-fon-block-elem:nth-child(2) {
    transform: translate(141px, -52px);
  }
  .advantage-fon-block .advantage-fon-block-elem:nth-child(3) {
    transform: translate(174px, 109px);
  }
  .advantage-description-number_val {
    font-size: 28px;
  }
  .advantage-description-number_name {
    font-size: 18px;
  }
  .advantage-description-number {
    margin-bottom: 20px;
  }
  .advantage-description-text {
    margin-bottom: 30px;
  }
}
@include media-custom(375px) {
}
