.best-real-estate {
  padding: 56px 0 100px;
  // .best-real-estate__title
  &__title {
    margin-bottom: 53px;
  }

  // .best-real-estate__row
  &__row {
    justify-content: space-between;
  }

  // .real-estate-info
  &__info {
    flex: 0 1 500px;
    margin-right: 20px;
    &-tab {
      display: none;

      opacity: 0;
      &_active {
        display: flex;

        opacity: 1;
      }
    }
  }

  // .best-real-estate__select
  &__select {
    flex: 0 1 556px;
    padding-top: 33px;
  }
}

// .real-estate-inf
.real-estate-info {
  flex-direction: column;
  align-items: flex-start;
  // .real-estate-info-image
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    padding: 13px;

    border: 1px solid #EFEFEF;
    border-radius: 2px;
    img,
    picture {
      width: 100%;
      height: auto;
    }
  }

  // .real-estate-info-subtitle
  &__subtitle {
  }

  // .real-estate-info-title
  &__title {
    margin-bottom: 7px;
  }

  // .real-estate-info-description
  &__description {
    margin-bottom: 34px;

    font-weight: 350;
    line-height: 160%;
  }

  // .real-estate-info-button
  &__button {
    width: 273px;
    padding: 21px 5px;

    color: $text-color;

    background: none;
    border: 1px solid $primary-color;
  }
}

.real-estate-select {
  // .real-estate-select__title
  &__title {
    justify-content: space-between;
    padding-bottom: 16px;
    span:first-child {
      margin-right: 5px;
    }
  }

  // .real-estate-select__list
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;
  }

  // .real-estate-select__item
  &__item {
    flex: 1 1 100%;
    justify-content: space-between;
    padding: 29px 20px;

    background: none;

    border: none;
    border-top: 1px solid #EFEFEF;

    transition: all 0.3s linear;
    &_active {
      background: #FFF;
      border-top-color: #FFF;
      border-radius: 2px;
      box-shadow: 0px 12px 40px rgba(121, 179, 220, 0.1);

      pointer-events: none;
      & + .real-estate-select__item {
        border-top-color: #FFF;
      }
    }
    &:hover{
      cursor: pointer;
    }
    &:last-child {
      border-bottom: 1px solid #EFEFEF;
    }
    // .real-estate-select__item-name
    &-name {
      margin-right: 5px;
    }
    // .real-estate-select__item-distance
    &-distance {
    }
  }

  // .real-estate-select__more
  &__more {
    margin-left: 20px;

    color: $primary-color;
  }
}

// <1280
@include media-laptop {
  .best-real-estate {
    padding: 50px 0;
    &__title {
      margin-bottom: 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .best-real-estate {
    padding: 30px 0;
  }
}
// <768
@include media-tablet {
  .best-real-estate {
    &__row {
      flex-direction: column-reverse;
      align-items: stretch;
    }
    &__info {
      flex: 1 1 100%;
      margin: 30px 0 0;
    }
    &__select {
      flex: 1 1 100%;
      padding-top: 0;
    }
  }
  .real-estate-info {
    &__image {
      width: 100%;
    }
  }
  .real-estate-select {
    &__list {
      flex-direction: row;
      flex-wrap: wrap;
    }
    &__item {
      flex: 1 1 50%;
    }
  }
}
// <480
@include media-mobile {
  .real-estate-select {
    &__item {
      flex: 1 1 100%;
    }
  }
}
