.project {
  padding-bottom: 100px;
}
.project-content {
}
.project-title {
  margin-bottom: 28px;

  color: $text-color;
  font-weight: normal;
  font-style: normal;
  line-height: 160%;
}

.project-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 64px;
}

.project-item-elem {
  max-width: 380px;
  padding: 8px 8px;

  border: 1px solid transparent;
  &__img {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
    &_foto {
      margin-right: 6px;
      margin-bottom: 6px;
    }
    &_foto:last-child{
      margin-right: 0;
    }
    &_foto:first-child{
      margin-right: 0;
    }
  }

  &__text {
    &_name {
      margin-bottom: 7px;

      color: $text-color;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__text-subname {
    display: flex;
    &_img {
      margin-right: 8px;
    }
    &_span {
      color: $text-color;
      font-weight: 350;
      font-style: normal;
      line-height: 160%;
    }
  }


}
.project-item-elem:hover {
  box-sizing: border-box;
  max-width: 380px;
  padding: 8px 8px;

  border: 1px solid #4AB7AE;
  border-radius: 2px;
  cursor: pointer;
}
.project-item-elem:active {
  box-sizing: border-box;
  max-width: 380px;
  padding: 8px 8px;

  border: 1px solid #4AB7AE;
  border-radius: 2px;
  cursor: pointer;
}
.project-item-elem:last-child{
  margin-right: 0;
}
.project-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__value {
  }

  &__option {
    margin-bottom: 44px;
    &_text {
      margin-bottom: 24px;

      color: $text-color;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__option-block {
    display: flex;
  }

  &__option-block-radio {
    display: flex;
    align-items: center;
    margin-right: 44px;

    cursor: pointer;
    &_input {
      position: absolute;
      z-index: -1;

      opacity: 0;
    }
    &_input+label {
      display: inline-flex;
      align-items: center;

      cursor: pointer;

      user-select: none;

    }
    &_input+label::before {
      display: inline-block;
      flex-grow: 0;
      flex-shrink: 0;
      box-sizing: border-box;
      width: 1em;
      height: 1em;
      margin-right: 0.5em;

      background-repeat: no-repeat;
      background-position: center center;
      background-size: 50% 50%;
      border: 1px solid #4AB7AE;
      border-radius: 4px;

      content: '';
    }
    &_input:not(:disabled):not(:checked)+label:hover::before{
      border-color: #B3D7FF;
    }
    &_input:not(:disabled):active+label::before {
      background-color: #B3D7FF;
      border-color: #B3D7FF;
    }
    &_input:focus+label::before{
      // box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }
    &_input:focus:not(:checked)+label::before{
      border-color: #80BDFF;
    }
    &_input:checked+label::before{
      background-image: url(../images/svg/icons/check.svg);
      background-position: center center;
      background-size: 77% 72%;
    }
    &_label {
      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__home {

    &_text {
      margin-bottom: 24px;

      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__home-block {
    display: flex;
    align-items: center;
    margin-bottom: 44px;
  }

  &__home-block-radio {
    position: relative;

    margin-right: 44px;
    &_input {
    }
    &_input {
      position: absolute;
      top: 4px;
      z-index: -1;

      opacity: 0;
    }
    &_input+label {
      display: inline-flex;
      align-items: center;

      cursor: pointer;

      user-select: none;

    }
    &_input+label::before {
      display: inline-block;
      flex-grow: 0;
      flex-shrink: 0;
      box-sizing: border-box;
      width: 1em;
      height: 1em;
      margin-right: 0.5em;

      background-repeat: no-repeat;
      background-position: center center;
      background-size: 77% 72%;
      border: 1px solid #4AB7AE;
      border-radius: 4px;

      content: '';
    }
    &_input:not(:disabled):not(:checked)+label:hover::before{
      border-color: #B3D7FF;
    }
    &_input:not(:disabled):active+label::before {
      background-color: #B3D7FF;
      border-color: #B3D7FF;
    }
    &_input:focus+label::before{
      // box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }
    &_input:focus:not(:checked)+label::before{
      border-color: #80BDFF;
    }
    &_input:checked+label::before{
      background-image: url(../images/svg/icons/check.svg);
      background-position: center center;
      background-size: 70% 70%;
    }
    &_label {
      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }

    &_img {
      // position: absolute;
      // left: 25px;
      margin-left: 15px;
    }
  }

  &__human {

    &_text {
      margin-bottom: 24px;

      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;

    }
  }

  &__human-block {
    display: flex;
    &_text {
      margin-bottom: 13px;

      color: #3E516D;
      font-weight: 350;
      font-style: normal;
      line-height: 21px;
      
    }
  }

  &__human-block-container {
    display: flex;
    flex-direction: column;
    margin-right: 120px;
  }
  &__human-block-container:last-child{
    margin-right: 0;
  }

  &__human-block-item {
    display: flex;
    align-items: center;
    &_minus {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      width: 32px;
      height: 32px;
      margin-right: 24px;

      border: 1px solid #EFEFEF;
      border-radius: 20px;
      cursor: pointer;

      &-elem{
        color: #4AB7AE;
      }
    }

    &_text {
      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 21px;
      text-align: center;
      border: none;
      background: none;
      width: 12px;
    }

    &_plus {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      width: 32px;
      height: 32px;
      margin-left: 24px;

      border: 1px solid #EFEFEF;
      border-radius: 20px;
      cursor: pointer;
      &-elem{
        color: #4AB7AE;
      }
    }
  }

  &__sum {
    display: flex;
    flex-direction: column;
  }

  &__sum-block {
    margin-bottom: 58px;

    text-align: center;

    background: #FFFFFF;
    border-radius: 2px;
    box-shadow: 0px 20px 40px rgba(166, 208, 238, 0.09);
    &_title {
      color: #3E516D;
      font-weight: normal;
      font-size: 28px;
      font-style: normal;
      line-height: 160%;
    }

    &_value {
      color: #4AB7AE;
      font-weight: 500;
      font-size: 48px;
      font-style: normal;
      line-height: 160%;
    }
  }

  &__sum-btn {

    &_input {
      padding: 20.5px 40.5px;

      color: #FFFFFF;
      font-weight: 500;
      font-style: normal;
      line-height: 23px;
      letter-spacing: 0.02em;

      background: #4AB7AE;
      border: none;
      border-radius: 2px;
      box-shadow: 0px 80px 118px rgba(0, 0, 0, 0.05), 0px 29.2013px 43.0719px rgba(0, 0, 0, 0.06), 0px 14.1767px 20.9106px rgba(0, 0, 0, 0.06), 0px 6.94968px 10.2508px rgba(0, 0, 0, 0.04), 0px 2.74791px 4.05317px rgba(0, 0, 0, 0.0434026);
      cursor: pointer;
    }
  }
}


//1200px
@include media-laptop {
  .project-item-elem{
    max-width: 280px;
  }
  .project-item-elem__img_foto{
    width: 30%;
  }
  .project-item-elem__img_foto:nth-child(1){
    width: 100%;
  }
  .project-item-elem__img{
    justify-content: center;
  }
  .project-item-elem:hover{
    max-width: 280px;
  }
  .project-info__home-block-radio{
    margin-right: 24px;
  }
  .project-info__option-block-radio{
    margin-right: 24px;
  }
  .project-info__home-block-radio_img{
    margin-left: 5px;
  }
}
//992px
@include media-tablet-horizontal {
  .project-info{
    flex-direction: column;
  }
  .project-info__value{
    margin-bottom: 30px;
  }
  .project-info__human-block{
    justify-content: center;
  }
  .project-info__human_text{
    text-align: center;
  }
}
//768px
@include media-tablet {
  .project-info__option-block-radio_label{
  }
}
// 480
@include media-mobile {
  .project-info__sum-block_title{
    font-size: 24px;
  }
  .project-info__sum-block_value{
    font-size: 36px;
  }
  .project-info__home-block{
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .project-info__home-block-radio{
    margin-bottom: 10px;
  }
  .project-info__option-block{
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .project-item{
    flex-wrap: wrap;
    justify-content: center;
  }
  .project-item-elem{
    max-width: 140px;
  }
  .project-item-elem:hover{
    max-width: 140px;
  }
  .project-info__sum-btn_input{
    padding: 20.5px 20.5px;
  }
  .project-info__sum-block{
    margin-bottom: 38px;
  }
  .project-info__human-block-container{
    margin-right: 0;
  }
  .project-info__human-block{
    justify-content: space-between;
  }
  .project-item{
    margin-bottom: 34px;
  }
}
@include media-custom (375px) {

}
