.cost-calculator {
  padding: 95px 0 100px;
  // .cost-calculator__title
  &__title {
    margin-bottom: 40px;
  }

  // .cost-calculator__change-sector
  &__change-sector {
    margin-bottom: 40px;
  }
}

@import './calculator-change-sector';

// <1280
@include media-laptop {
  .cost-calculator {
    padding: 80px 0;
    &__title{
      margin-bottom: 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .cost-calculator {
    padding: 50px 0;
  }
}
// <768
@include media-tablet {
  .cost-calculator {
    padding: 30px 0;
  }
}
