.team {
  padding-top: 200px;
}
.team-title {
  margin-bottom: 33px;

  color: #3E516D;
  font-weight: 500;
  font-style: normal;
  line-height: 160%;
}
.team-subtitle {
  margin-bottom: 112px;

  color: #3E516D;
  font-weight: 350;
  font-style: normal;
  line-height: 160%;
}
.team-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 100px;

  background: #F4F6F8;

}
.team-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 120px;
  padding: 0px 120px;
  max-width: 1300px;
}
.team-block:last-child{
  margin-bottom: 0;
}
.team-block-item {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 360px;
  margin-right: 20px;
  text-align: center;
  &_img {
    position: absolute;
    top: -60px;
  }

  &__elem {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 360px;

    height: 240px;
    padding: 0px 18px 30px 18px;

    background: linear-gradient(180deg, #F5F7F9 0%, #FFFFFF 100%);
    &_name {
      margin-bottom: 8px;

      color: #3E516D;
      font-weight: normal;
      font-style: normal;
      line-height: 160%;
    }

    &_status {
      margin-bottom: 12px;

      color: #3E516D;
      font-weight: 350;
      font-style: italic;
      line-height: 160%;
    }

    &_point {
      max-width: 324px;

      color: #3E516D;
      font-weight: 300;
      font-style: italic;
      line-height: 160%;
    }
  }
}
.team-block-item :last-child{
  margin-right: 0;
}
.elem-padding {
  padding: 0px 18px 10px 18px;
}
@include media-custom (1370px){
  .team-block{
    padding: 0px 40px;
  }
}
//1200px
@include media-laptop {
    .team-block{
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 60px;
    }
    .team-block-item{
      margin-right: 20px;
      margin-bottom: 80px;
    }
}
//992px
@include media-tablet-horizontal {
  .team-block-item__elem{
    width: 224px;
  }
  .team-block-item_img{
    width: 60%;
  }
  .team-block{
    padding: 0px 0px;
  }
}
//768px
@include media-tablet {

}
// 480
@include media-mobile {
  .team-block-item{
    margin-right: 20px;
  }
}
@include media-custom (375px) {
  .team-block-item{
    margin-right: 0px;
  }
  .team-wrapper{
    padding-bottom: 40px;
  }
}
// 320
@include media-mobile-small {

}
