.home-banner {
  position: relative;
  z-index: 1;

  width: 100%;
  min-height: 600px;
  padding-top: 132px;

  background: url('../images/main-banner.jpg') 50% 50% / cover no-repeat;
  &::after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    width: 50%;
    height: 100%;

    background: rgba(18, 26, 54, 0.6);
    backdrop-filter: blur(10px);

    content: '';
  }
  // .home-banner__content
  &__content {
    max-width: 50%;
  }

  // .home-banner__title
  &__title {
    margin-bottom: 8px;

    color: #FFF;
    font-weight: 400;
    letter-spacing: 0.05em;
  }

  // .home-banner__description
  &__description {
    max-width: 480px;
    margin-bottom: 44px;

    color: #FFF;
    font-weight: 400;
    letter-spacing: 0.01em;
  }

  // .home-banner__button

  &__button {
    width: 100%;
    max-width: 447px;
  }
}

// <1280
@include media-laptop {
}
// <992
@include media-tablet-horizontal {
  .home-banner {
    &::after {
      width: 60%;
    }
  }
}
// <768
@include media-tablet {
  .home-banner {
    &::after {
      width: 80%;
    }
    &__content {
      max-width: 75%;
    }
  }
}
// <480
@include media-mobile {
  .home-banner {
    align-items: center;
    height: calc(100vh - 58px);
    min-height: auto;
    padding: 0;
    &::after {
      width: 100%;
    }
    &__content {
      max-width: none;

      text-align: center;
    }
    &__button {
      max-width: 80%;
    }
  }
}
// <320
@include media-mobile-small {
