.architect-review {
  padding: 100px 0 135px;
  // .architect-review__title
  &__title {
    margin-bottom: 54px;
  }

  // .architect-review__content
  &__content {
  }

  // .architect-review__comment
  &__comment {
    flex: 1 1 569px;
  }
  &__items {
    display: grid;
    grid-auto-flow: row dense;
    grid-template-areas:
      'item-1 item-1 item-1 item-3'
      'video video video item-3'
      'item-2 item-2 item-2 item-2';
    grid-template-rows: 111px 236px 174px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 1px 1px;
    max-width: 580px;
    margin-top: 50px;
    margin-left: 16px;
  }
  &__item {
    &.item-1 {
      display: grid;
      grid-area: item-1;
      grid-template-areas: '. .';
      grid-template-rows: 111px;
      gap: 0 1px;
    }
    &_video {
      position: relative;

      display: grid;
      grid-area: video;
      grid-template-areas: '.';
      grid-template-rows: 236px;
    }
    &.item-2 {
      display: grid;
      grid-area: item-2;
      grid-template-areas: '. . . .';
      grid-template-rows: 90px;
      gap: 0 1px;
    }
    &.item-3 {
      display: grid;
      grid-area: item-3;
      grid-template-areas:
        '.'
        '.';
      grid-template-columns: 212px;
      gap: 1px 0;
    }
    &-image {
      display: flex;
      img,
      picture {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  // .architect-review__video-btn
  &__video-btn {
    position: absolute;
    top: 50%;
    left: 50%;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 87px;
    height: 87px;
    overflow: hidden;

    background: rgba(74, 183, 174, 0.52);

    border-radius: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    backdrop-filter: blur(12px);

    animation: animate-opacity 1.5s infinite linear alternate;
    img {
      width: 30%;
      height: auto;
      margin-left: 5px;
    }
  }
}

.architect-review-comment {
  position: relative;

  padding: 32px 40px 36px;

  background: #FFF;
  box-shadow: 0px 30px 60px rgba(121, 179, 220, 0.06);
  &::after {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    background: #FFFFFF;

    box-shadow: 0px 30px 60px rgba(121, 179, 220, 0.06);
    transform: rotate(-2deg);

    content: '';
  }
  // .architect-review-comment__image
  &__image {
    display: flex;
    width: 96px;
    height: 96px;
    margin-bottom: 32px;
    overflow: hidden;

    border-radius: 50%;
    picture {
      width: 100%;
      height: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .architect-review-comment__text
  &__text {
    margin-bottom: 50px;

    color: $text-color;
    font-weight: 350;
    font-style: italic;
    line-height: 165%;
  }

  // .architect-review-comment__signature
  &__signature {
    display: flex;
    justify-content: flex-end;
    padding-right: 29px;
  }
}

@keyframes animate-opacity {
  to {
    opacity: 0.6;
  }
  from {
    opacity: 1;
  }
}

// <1280
@include media-laptop {
  .architect-review {
    padding: 50px 0;
    &__title {
      margin-bottom: 30px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .architect-review {
    padding: 30px 0;
    &__video-btn {
      width: 50px;
      height: 50px;
    }
  }
}
// <768
@include media-tablet {
  .architect-review {
    &__content {
      flex-direction: column;
    }
    &__items {
      flex: 0 1 50%;
      margin: 50px auto 0;
    }
    .architect-review-comment {
      padding: 25px 20px;
      &__text {
        font-size: 14px;
      }
    }
  }
}
// <480
@include media-mobile {
  .architect-review{
    &__items{
      grid-auto-flow: row dense;
      grid-template-areas:
        'item-1 item-1'
        'item-3 item-3'
        'video video'
        'item-2 item-2';
      grid-template-rows: 150px 150px 225px 300px;
      grid-template-columns: 1fr 1fr;
    }
    &__item {
      &.item-1 {
        grid-template-rows: 150px;
        gap: 0 1px;
      }
      &_video {
        grid-template-areas: '.';
        grid-template-rows: 225px;
      }
      &.item-2 {
        grid-template-areas: 
          '. .'
          '. .';
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        gap: 1px;
      }
      &.item-3 {
        grid-template-areas: '. .';
        grid-template-rows: 150px;
        grid-template-columns: 1fr 1fr;
        gap: 1px;
      }
    }
  }
}
// <320
@include media-mobile-small {
}
