.get-event-archive {
  position: relative;
  // .get-event-archive__content
  display: flex;
  height: 542px;
  padding: 100px 0 120px;

  color: #FFF;

  background-color: $primary-dark-color;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 448px;
  }

  // .get-event-archive__title

  &__title {
    margin-bottom: 15px;

    font-weight: 400;
    font-size: 32px;
    line-height: 1.3;
    letter-spacing: 0.02em;
  }

  // .get-event-archive__description

  &__description {
    font-weight: 350px;
    letter-spacing: 0em;
  }

  // .get-event-archive__form

  &__form {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 48px;
  }

  // .get-event-archive__form-label

  &__form-label {
    display: flex;
    align-items: center;
    height: 64px;
    padding: 0 30px 0 28px;

    border: 1px solid $primary-color;
  }

  // .get-event-archive__form-label-icon

  &__form-label-icon {
    margin-right: 13px;
  }

  // .get-event-archive__form-input

  &__form-input {
    width: 100%;

    color: #FFFFFF;

    line-height: 21px;

    letter-spacing: 0.02em;

    background: none;
    border: none;
    &::placeholder {
      color: #D6D6D6;
    }
  }

  // .get-event-archive__button

  &__button {
    margin-top: 24px;
  }

  // .get-event-archive__video

  &__video {
    position: absolute;
    top: 0;
    right: 0;

    width: 50%;
    height: 100%;

    picture,
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

// <1280
@include media-laptop {
  .get-event-archive {
    align-items: center;
    padding: 80px 0;
    &__title {
      font-size: 28px;
    }
  }
}
// <992
@include media-tablet-horizontal {
  .get-event-archive {
    background: rgba($color: $primary-dark-color, $alpha: 0.7);
    &__title {
      font-size: 26px;
    }
    &__content {
      width: 100%;
      max-width: none;
    }
    &__video {
      z-index: -1;

      width: 100%;
    }
    &__form {
      width: 400px;
    }
  }
}
// <768
@include media-tablet {
  .get-event-archive {
    &__title{
      font-size: 24px;
    }
    &__form {
      width: 100%;
    }
  }
}
