.contacts {
  padding-top: 40px;
  padding-bottom: 180px;
}
.contacts-title {
  margin-bottom: 60px;

  color: $text-color;
  font-weight: 500;
  font-style: normal;
  line-height: 160%;
}
.contacts-container {
  position: relative;
}
.contacts-map {
  box-sizing: border-box;

  border: 12px solid #FFFFFF;
  filter: drop-shadow(0px 30px 60px rgba(121, 179, 220, 0.06));
  &__img {
    display: flex;
    width: 100%;

    transform: translateY(8px);
    img{
      width: 100%;
      height: 100%;
    }

  }

  &__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 42px 72px 50px 59px;

    background: #093049;
  }

  &__block-elem {

    &_text {
      margin-bottom: 4px;

      color: #FFFFFF;
      font-weight: normal;
      font-style: normal;
      line-height: 18px;
    }

    &_link {
      color: $primary-color;
      font-weight: normal;
      font-style: normal;
      line-height: 26px;
      text-decoration: none;

      outline: none;
      cursor: pointer;
    }

  }
}
.contacts-bg {
  position: absolute;
  right: -40px;
  bottom: -60px;
  z-index: -1;

  box-sizing: border-box;
  width: 1170px;
  height: 440px;

  border: 1px solid #4AB7AE;
}

//1200px
@include media-laptop {
  .contacts-bg{
    width: 100%;
  }
}
//992px
@include media-tablet-horizontal {
  .contacts-bg{
    display: none;
  }
}
//768px
@include media-tablet {
  .contacts{
    padding-bottom: 40px;
  }
  .contacts-map__block{
    padding: 42px 10px 20px 10px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .contacts-map__block-elem{
    margin-right: 20px;
    margin-bottom: 10px;
  }
  .contacts-map__block-elem:last-child{
    margin-right: 0;
  }
}
// 480
@include media-mobile {
  .contacts-title{
    margin-bottom: 20px;
  }
}
@include media-custom (375px) {
  .contacts-map__block-elem{
    margin-right: 0px;
  }
  .contacts-map__block{
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
  }
}
// 320
@include media-mobile-small {

}
