.about {
  padding-top: 40px;
}
.about-title {
  color: #3E516D;
  font-weight: 500;
  font-style: normal;
  line-height: 160%;
}
.about-content {
  display: flex;
  align-items: center;
  margin-top: 60px;
}
.about-block {
  margin-right: 40px;
  &__text {
    max-width: 368px;
    margin-bottom: 20px;

    color: #3E516D;
    font-weight: normal;
    font-style: normal;
    line-height: 160%;
    &_span {
      font-weight: 500; 
                
    }
  }

  &__text:nth-child(3){
    max-width: 240px;
    margin-bottom: 0;
  }
}
.about-item {
  position: relative;
    
}
.about-item-block {
  box-sizing: border-box;

  border: 12px solid #FFFFFF;
  filter: drop-shadow(0px 30px 60px rgba(121, 179, 220, 0.03));
  &_img {
    max-width: 712px;

    transform: translateY(7px);
  }
}
.about-item-bg {
  position: absolute;
  top: 48px;
  right: -48px;
  z-index: -1;

  box-sizing: border-box;
  width: 712px;
  height: 472px;

  border: 1px solid #4AB7AE;
}

// fabric
.about-fabric {
  display: flex;
  align-items: center;
  padding-top: 160px;
  padding-bottom: 50px;
}
.about-fabric-img {
  position: relative;
  &_img {
    width: 546px;
    height: 625px;
  }

  &_block {
    position: absolute;
    top: 128px;
    right: -49px;
    z-index: -1;

    box-sizing: border-box;
    width: 546px;
    height: 625px;

    border: 1px solid #4AB7AE;
  }
}
.about-fabric-item {
  position: relative;

  margin-left: 128px;
  &__block {
    width: 100%;
    padding: 77px 61px 81px 33px;

    background: #FFFFFF;
    box-shadow: 0px 30px 60px rgba(121, 179, 220, 0.06);
    &_title {
      max-width: 380px;
      margin-bottom: 30px;

      color: #3E516D;
      font-weight: 500;
      font-style: italic;
      line-height: 140%;
      letter-spacing: 0.04em;
    }

    &_text {
      margin-bottom: 20px;

      color: #3E516D;
      font-weight: normal;
      font-style: italic;
      line-height: 160%;
    }
    &_text:last-child{
      margin-bottom: 0;
    }
  }

  &__elem {
    position: absolute;
    top: 0;
    left: -4px;
    z-index: -1;

    width: 496px;
    height: 614px;

    background: #FFFFFF;
    box-shadow: 0px 30px 60px rgba(121, 179, 220, 0.06);
    transform: rotate(-2deg);
    transform: rotate(-3deg);
  }
}


//1200px
@include media-laptop {
  .about-item-block_img{
    max-width: 519px;
  }
  .about-item-bg {
    width: 560px;
    height: 354px;
  }
  .about-fabric-img_img {
    width: 354px;
    height: 429px;
}
.about-fabric-img_block {
  width: 411px;
    height: 353px;
}
.about-fabric-item__elem{
  height: 500px;
}
}
//992px
@include media-tablet-horizontal {
  .about-content{
    flex-direction: column;
    align-items: flex-start;
  }
  .about-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.about-item-bg{
  right: 170px;
}
.about-fabric{
  padding-top: 80px;
  flex-direction: column-reverse;
  align-items: flex-start;
}
.about-fabric-item{
  margin-left: 0;
}
.about-fabric-img{
  display: flex;
  justify-content: center;
  width: 100%;
}
.about-fabric-img_block{
  top: 98px;
  right: 248px;
}
.about-fabric-item__elem{
  height: 406px;
  width: 100%;
}
}
//768px
@include media-tablet {
  .about-fabric-item__block_title{
    margin-bottom: 10px;
  }
  .about-fabric-item{
    margin-bottom: 60px;
  }
  .about-fabric-img_block{
    right: 0;
    left: 0;
  }
  .about-item-bg{
    right: 0;
  }
  .about-block__text{
    max-width: 100%;
  }
  .about-item-block_img{
    max-width: 100%;
  }
}
// 480
@include media-mobile {
.about-item-bg{
  display: none;
}
.about-fabric-img_img{
  width: 100%;
  height: 100%;
}
.about-fabric-img_block{
  display: none;
}
.about-content{
  margin-top: 30px;
}
}
