.plot {
  padding-top: 80px;
  padding-bottom: 80px;

  background: #F5F7F8;

}

.plot-content {
}
.swiper-container-plot {
  position: relative;
	
  &__button {

    position: absolute;
    top: 50%;
    z-index: 10;
		
    display: flex;
    align-items: center;
    justify-content: center;
		
    width: 60px;
    height: 60px;
    margin-top: 10px;
		
    background: none;
    border: 1px solid $primary-color;
    border-radius: 2px;
    transform: translate(0, -50%);
    cursor: pointer;

    transition: all .3s ease-in-out;
    &:hover:not(&_disabled){
      background: $primary-color;
      svg{
        fill:#FFF
      }
    }
    svg{
      transition: all .3s ease-in-out;
		
      fill: $primary-color;
    }
    &_disabled {
      border-color:#B6BDC8;

      pointer-events: none;
      svg{
        fill: #B6BDC8;
      }
    }  
    &_prev {
      left: 0;
    }

    &_next {
      right: 0;
    }
  }
}
.swiper-wrapper {
}
.swiper-slide {
}
.plot-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 80px;

}
.plot-block-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.plot-block-content-title {
  width: 100%;
  margin-bottom: 17px;
  padding-left: 55px;

  color: #3E516D;
  font-weight: 500;
  font-style: normal;
  line-height: 52px;
  text-align: start;
}

.plot-block-content-text {
  max-width: 422px;
  margin-bottom: 35px;

  color: #76828F;
  font-weight: normal;
  font-style: normal;
  line-height: 140%;
}

.plot-block-content-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 483px;
  margin-bottom: 32px;
  &__elem {
    box-sizing: border-box;
    width: 212px;
    padding: 25.5px 0px;

    text-align: center;

    border: 1px solid #EFEFEF;
    cursor: pointer;

    transition: 0.3s;

    &_text {
      color: #3E516D;
      font-weight: 350;
      font-style: normal;
      line-height: 21px;

      transition: 0.3s;

    }
  }
  &__elem:hover {
    box-sizing: border-box;

    background: #4AB7AE;
    border: 1px solid #4AB7AE;
    opacity: 0.7;

    transition: 0.3s;

  }
  &__elem:hover .plot-block-content-item__elem_text{
    color: #FFFFFF;

    transition: 0.3s;
  }
}
.plot-block-content-val {
  display: flex;
  align-items: center;
  justify-content: center;
}
.plot-block-content-val-item {
  display: flex;
  align-items: center;
  margin-right: 24px;
  &_img {
    margin-right: 8px;
  }
  &_img:last-child{
    margin-right: 0;
  }

  &_text {
    color: #3E516D;
    font-weight: normal;
    font-style: normal;
    line-height: 160%;
  }
}

.plot-block-img {
}
.plot-block-img-block {
  box-sizing: border-box;
  margin-bottom: 24px;
  padding: 16px 16px;

  border: 1px solid #EFEFEF;
  border-radius: 2px;
  &_img {
  }
}
.plot-block-img-btn {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.plot-block-img-btn-button {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 32px;

  border: 1px solid #4AB7AE;
  cursor: pointer;
  &_img {
    margin-right: 16px;
  }

  &_text {
  }
}


//1200px
@include media-laptop {
  .plot-block-content-val-item{
    margin-right: 10px;
  }
  .plot-block-img-block_img{
    width: 100%;
  }
  .plot-block-content-item__elem{
    width: 160px;
  }
}
//992px
@include media-tablet-horizontal {
  .plot-block-content-item__elem{
    width: 100px;
  }
}
//768px
@include media-tablet {
.plot-block-content-val{
  flex-wrap: wrap;
}
.plot-block-content-val-item{
  margin-bottom: 10px;
}
.plot-block-img-btn-button{
  padding: 20px 15px;
}
.plot-block{
  flex-direction: column;
}
.plot-block-img-btn{
  justify-content: center;
}
.plot-block-content-text{
  margin-bottom: 15px;
  text-align: center;
}
.plot-block-content-title{
  padding-left: 0;
  text-align: center;
}
}
// 480
@include media-mobile {
  .plot{
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .swiper-container-plot__button{
    width: 30px;
    height: 30px;
  }
  .plot-block{
    padding: 0px 30px;
  }
}
